<template>
    <el-card  v-loading="loading">
    <slot-common slot='header' style="font-size:18px;">
    <!-- 面包屑的插槽 具名插槽  title-->
    <template slot='title'>素材管理</template>
   </slot-common>
   <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="全部图片" name="first">
        <div class="img-list">
            <el-card class="img-card" v-for="item in list" :key="item.id">
                <img :src="item.url" alt="">
                <el-row align="middle" class="operate" type="flex" justify="space-around">
              <i class="el-icon-star-on"></i>
              <i class="el-icon-delete-solid"></i>
            </el-row>
            </el-card>
        </div>
         <el-row type='flex' justify="center">
        <el-pagination
        background
        layout="prev, pager, next"
        :total="1000">
        </el-pagination>
         </el-row>
    </el-tab-pane>
    <el-tab-pane label="收藏图片" name="second">

    </el-tab-pane>
  </el-tabs>
    </el-card>
</template>

<script>
export default {
  data () {
    return {
      activeName: 'first',
      list: [],
      loading: false
    }
  },
  methods: {
    handleClick () {
      this.$http({
        url: '/user/images',
        params: { collect: false }
      }).then(result => {
        this.list = result.data.results
      })
    }
  },
  created () {
    this.handleClick()
  }
}
</script>

<style lang="less" scoped>
.img-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  .img-card {
    width: 200px;
    height: 200px;
    margin: 20px 10px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
    }
    .operate {
      position: absolute;
      bottom: 0;
      left: 0;
      height: 30px;
      width: 100%;
      background-color: #f4f5f6;
      i {
        font-size: 22px;
      }
    }
  }
}
</style>
